import { Card, CardBody, CardFooter, CardHeader, CardProps, LinkBox, LinkOverlay, Text, useColorModeValue } from "@chakra-ui/react";
import React, { FC } from "react";
import { Link } from "../atoms";

export type LinkCardProps = Omit<CardProps, "title"> & {
  to: string;
  title?: React.ReactNode | null;
  footer?: React.ReactNode | null;
};

export const LinkCard: FC<LinkCardProps> = ({ title, to, footer, children, ...rest }) => {
  const bg = useColorModeValue("gray.50", "gray.700");
  const hoverBg = useColorModeValue("gray.100", "gray.600");
  return (
    <LinkBox as={Card} transition="all 0.5s" background={bg} _hover={{ background: hoverBg }} {...rest}>
      {title && (
        <CardHeader fontSize="large">
          <LinkOverlay as={Link} to={to} _hover={{ textDecoration: "none" }}>
            {title}
          </LinkOverlay>
        </CardHeader>
      )}
      <CardBody>{children}</CardBody>
      {footer && <CardFooter>{footer}</CardFooter>}
    </LinkBox>
  );
};
